.home{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 320px;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.title{
    font-weight: 300;
    font-size: 15px;
    color: white;
    padding-bottom: 7px;
}
.svg-obj {
    /* position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); */
}
.path {
    fill: #80ffd3;
    fill-opacity: 0;
    stroke: #80ffd3;
    /* stroke: #E55968; */
    stroke-width: 5;
    stroke-linecap: round;
    stroke-dasharray: 860;
    stroke-dashoffset: 860;
    animation: draw 2s linear forwards;
}
@keyframes draw {
    to {
        stroke-dashoffset: 0;
    }
}
@-webkit-keyframes draw {
    to {
        stroke-dashoffset: 0;
    }
}
@-moz-keyframes draw {
    to {
        stroke-dashoffset: 0;
    }
}
@-o-keyframes draw {
    to {
        stroke-dashoffset: 0;
    }
}
.content{
    /* position: absolute;
    top: 57%;
    left: 50%;
    transform: translate(-50%,-50%); */
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}
.slider-wrapper {
    font-size: 19.5px;
    color:#fff;
    font-weight: bold;
    text-transform: uppercase;
    display: flex;
    text-align: center;
    text-align: center;
    align-items: center;
    justify-content: center;
}
.slider{
    text-align: center;
    height: 50px;
    overflow: hidden;
}
.slider div {
    color:#e0a557;
    width: 200px;
    height: 50px;
    margin-bottom: 50px;
    padding: 0px 10px;
    text-align: center;
    box-sizing: border-box;
}
.slider-text1 {
    display: flex;
    align-items: center;
    background: transparent;
    animation: slide 5s linear infinite;
}
.slider-text2 {
    display: flex;
    align-items: center;
    background: transparent;
}
.slider-text3 {
    display: flex;
    text-align: center;
    align-items: center;
    background: transparent;
}
@keyframes slide {
    0% {margin-top:-300px;}
    5% {margin-top:-200px;}
    33% {margin-top:-200px;}
    38% {margin-top:-100px;}
    66% {margin-top:-100px;}
    71% {margin-top:0px;}
    100% {margin-top:0px;}
}
.buttons{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.btn9{
    padding: 20px 10px;
    margin:10px 4px;
    color: #fff;
    text-transform: uppercase;
    text-align: center;
    position: relative;
    text-decoration: none;
    display:inline-block;
    overflow:hidden;
/*    border:1px solid #6098FF; */
}
.btn9 span{
    background:transparent;
    width:100%;
    position:relative;
    padding:10px 10px;
    -moz-transition: all .65s cubic-bezier(0.77, 0, 0.175, 1);
    -o-transition: all .65s cubic-bezier(0.77, 0, 0.175, 1);
    -webkit-transition: all .65s cubic-bezier(0.77, 0, 0.175, 1);
    transition: all .65s cubic-bezier(0.77, 0, 0.175, 1);
    /* border:1px solid #6098FF;  */
}
.transition{
    position: absolute;
    top: -10%;
    left: 0%;
    width: 100%;
    height: 0%;
    background: #80ffd3;
    /* background: #E55968; */
    z-index:-1;
    -ms-transform: skewX(-50deg); 
    -webkit-transform: skewX(-50deg); 
    transform: skewX(-50deg);
}
.btn9:hover span{
    color: #80ffd3!important;
    /* color: #E55968!important; */
}
.btn9:hover .transition{
    height:100%;
    top:101%;
    -moz-transition: all .7s cubic-bezier(0.77, 0, 0.175, 1);
    -o-transition: all .7s cubic-bezier(0.77, 0, 0.175, 1);
    -webkit-transition: all .7s cubic-bezier(0.77, 0, 0.175, 1);
    transition: all .7s cubic-bezier(0.77, 0, 0.175, 1);
}