.container{
    width: 200px;
    height: 15vh;
    background:transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
    position: absolute;
}
.head{
    border-bottom: .5px solid white;
    padding-bottom: 10px;
}
.head span{
    color: #E55968;
}
.social{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.social .icon1, .icon2, .icon3{
    padding:10px;
}
.social a{
    font-size: 30px;
    text-decoration: none;
    color: white;
}
.icon1 a:hover{
    color: #dd265d;
}
.icon2 a:hover{
    color: #3f3c3c;
}
.icon3 a:hover{
    color: #0072b1;
}
.icon4 a:hover{
    color: #DC463B;
}