.project{
    width: 100%;
    height: 100vh;
    background: #58D68D;
    background: url(https://wallpaperaccess.com/full/872651.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    backdrop-filter: blur(8px);
    /* display: flex; */
    align-items: flex-start;
    justify-content: center;
    position: relative;
}
.heading{
    /* margin-top: 50px; */
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    font-size: 20px;
    background: #58D68D;
    /* filter: blur(8px); */
    width: 100%;
    height: 20vh;
}
.project-content{
    display: flex;
    position: absolute;
    bottom: 0px;
    float: inline-end;
    width: 100%;
    height: 40vh;
    background:turquoise;
}